.home-page {
    height: 90%;
    background: url(../../svg/white-wave-bg.svg) center bottom -0.5px / 100% 14% no-repeat scroll padding-box border-box, linear-gradient(60deg, rgb(82, 67, 170), rgb(237, 80, 180)) 0% 0% / auto repeat scroll padding-box border-box rgb(82, 67, 170);
    background-blend-mode: normal, normal;
    color: rgb(255, 255, 255);
    font-family: Charlie-Text;
}

.main-home-page {

    .home-page-content {
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-top: 40px;
    }


    .home-page-title {
        font-size: 2rem;
        line-height: 1.25;
        font-weight: 500;
        margin: 0;
    }

    .home-page-p {
        font-size: 20px;
        margin-top: 7px;
        font-family: Charlie-Text;
        // max-width: 500px;
    }

    .home-page-btn {
        appearance: none;
        -webkit-box-align: center;
        align-items: center;
        border-radius: 0.3rem;
        cursor: pointer;
        display: inline-flex;
        transition: all 250ms ease-out 0s;
        background-color: rgb(0, 101, 255);
        border: 1px solid rgb(0, 101, 255);
        text-decoration: none;
        font-size: 1rem;
        padding: 15px 41px;
        color: #fff;



        &:hover {
            background-color: rgb(7, 71, 166);
            border-color: rgb(7, 71, 166);
            text-decoration: none;
        }

    }

}

.sec-home-container {
    max-width: 1096px;
    margin-left: auto;
    margin-right: auto;
    color: $clr2;

    .main-sec-container {
        background: linear-gradient(0deg, rgb(230, 252, 255), rgb(255, 255, 255)) 0% 0% / auto repeat scroll padding-box border-box rgb(178, 212, 255);
        background-blend-mode: normal;
        width: 100%;
    }


    .sec-container-card {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: left;
        width: 53.33%;
        gap: 10px;

        h2 {
            font-size: 36px;
            font-family: Charlie-Semi-Bold;
        }

    }

    .sec-container-title {
        font-size: 2rem;
    }

    .sec-container-content {

        margin-top: 50px;
        display: grid;
        grid-template-columns: 350px 1fr;
        grid-template-areas: "card img ";
        gap: 32px;

    }

    .home-cards-container {
        grid-area: 'card';
    }

    .home-card {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-bottom: 50px;
        color: #091e42;
        font-size: 16px;
    }

    .home-card-title {
        font-family: Charlie-Semi-Bold;
    }



    .card-img {
        grid-area: 'img';
    }

}


